import router from '@/router'
import {
  cloneDeep
} from 'lodash'
import util from '@/utils'

export default context => ({
  namespaced: true,
  actions: {
    /**
     * @description  | 
     * @description  dbName.path = value
     * @param {Object} vuex context
     * @param {Object} payload dbName {String} 
     * @param {Object} payload path {String} 
     * @param {Object} payload value {*} 
     * @param {Object} payload user {Boolean} 
     */
    set (context, {
      dbName = 'database',
      path = '',
      value = '',
      user = false
    }) {
      util.db.dbSet({
        dbName,
        path,
        value,
        user
      })
    },
    /**
     * @description 
     * @description  dbName.path || defaultValue
     * @param {Object} vuex context
     * @param {Object} payload dbName {String} 
     * @param {Object} payload path {String} 
     * @param {Object} payload defaultValue {*} 
     * @param {Object} payload user {Boolean} 
     */
    get (context, {
      dbName = 'database',
      path = '',
      defaultValue = '',
      user = false
    }) {
      return util.db.dbGet({
        dbName,
        path,
        defaultValue,
        user
      })
    },
    /**
     * @description 
     * @param {Object} vuex context
     * @param {Object} payload user {Boolean} 
     */
    database (context, {
      user = false
    } = {}) {
      return util.db.database({
        user,
        defaultValue: {}
      })
    },
    /**
     * @description 
     * @param {Object} vuex context
     * @param {Object} payload user {Boolean} 
     */
    databaseClear (context, {
      user = false
    } = {}) {
      return util.db.database({
        user,
        validator: () => false,
        defaultValue: {}
      })
    },
    /**
     * @description  [  ]
     * @param {Object} vuex context
     * @param {Object} payload basis {String}  [ name | path | fullPath ]
     * @param {Object} payload user {Boolean} 
     */
    databasePage (context, {
      basis = 'fullPath',
      user = false
    } = {}) {
      return util.db.database({
        path: `$page.${router.app.$route[basis]}`,
        user,
        defaultValue: {}
      })
    },
    /**
     * @description  [  ]
     * @param {Object} vuex context
     * @param {Object} payload basis {String}  [ name | path | fullPath ]
     * @param {Object} payload user {Boolean} 
     */
    databasePageClear (context, {
      basis = 'fullPath',
      user = false
    } = {}) {
      return util.db.database({
        path: `$page.${router.app.$route[basis]}`,
        user,
        validator: () => false,
        defaultValue: {}
      })
    },
    /**
     * @description  ( $data ) 
     * @param {Object} vuex context
     * @param {Object} payload instance {Object} vue 
     * @param {Object} payload basis {String}  [ name | path | fullPath ]
     * @param {Object} payload user {Boolean} 
     */
    pageSet (context, {
      instance,
      basis = 'fullPath',
      user = false
    }) {
      return util.db.database({
        path: `$page.${router.app.$route[basis]}.$data`,
        user,
        validator: () => false,
        defaultValue: cloneDeep(instance.$data)
      })
    },
    /**
     * @description 
     * @param {Object} vuex context
     * @param {Object} payload instance {Object} vue 
     * @param {Object} payload basis {String}  [ name | path | fullPath ]
     * @param {Object} payload user {Boolean} 
     */
    pageGet (context, {
      instance,
      basis = 'fullPath',
      user = false
    }) {
      return util.db.dbGet({
        path: `$page.${router.app.$route[basis]}.$data`,
        user,
        defaultValue: cloneDeep(instance.$data)
      })
    },
    /**
     * @description 
     * @param {Object} vuex context
     * @param {Object} payload basis {String}  [ name | path | fullPath ]
     * @param {Object} payload user {Boolean} 
     */
    pageClear (context, {
      basis = 'fullPath',
      user = false
    }) {
      return util.db.database({
        path: `$page.${router.app.$route[basis]}.$data`,
        user,
        validator: () => false,
        defaultValue: {}
      })
    }
  }
})
