import low from 'lowdb'
import LocalStorage from 'lowdb/adapters/LocalStorage'
import * as cookies from './cookies'
import {
  cloneDeep
} from 'lodash'

const adapter = new LocalStorage(`tao-${process.env.VUE_APP_VERSION}`)
const db = low(adapter)
db.defaults({
  sys: {},
  database: {}
}).write()

/**
 * @description  
 * @param {Object} payload dbName {String} 
 * @param {Object} payload path {String} 
 * @param {Object} payload user {Boolean} 
 * @param {Object} payload validator {Function}   true 
 * @param {Object} payload defaultValue {*} 
 * @returns {String} 
 */
function pathInit ({
  dbName = 'database',
  path = '',
  user = true,
  validator = () => true,
  defaultValue = ''
}) {
  const uuid = cookies.get('uuid') || 'ghost-uuid'
  const currentPath = `${dbName}.${user ? `user.${uuid}` : 'public'}${path ? `.${path}` : ''}`
  const value = db.get(currentPath).value()
  if (!(value !== undefined && validator(value))) {
    db
      .set(currentPath, defaultValue)
      .write()
  }
  return currentPath
}

/**
 * @description  | 
 * @description  dbName.path = value
 * @param {Object} payload dbName {String} 
 * @param {Object} payload path {String} 
 * @param {Object} payload value {*} 
 * @param {Object} payload user {Boolean} 
 */
export function dbSet ({
  dbName = 'database',
  path = '',
  value = '',
  user = false
}) {
  db.set(pathInit({
    dbName,
    path,
    user
  }), value).write()
}

/**
 * @description 
 * @description  dbName.path || defaultValue
 * @param {Object} payload dbName {String} 
 * @param {Object} payload path {String} 
 * @param {Object} payload defaultValue {*} 
 * @param {Object} payload user {Boolean} 
 */
export function dbGet ({
  dbName = 'database',
  path = '',
  defaultValue = '',
  user = false
}) {
  return cloneDeep(db.get(pathInit({
    dbName,
    path,
    user,
    defaultValue
  })).value())
}

/**
 * @description 
 * @param {Object} payload user {Boolean} 
 */
export function database ({
  dbName = 'database',
  path = '',
  user = false,
  validator = () => true,
  defaultValue = ''
} = {}) {
  return db.get(pathInit({
    dbName,
    path,
    user,
    validator,
    defaultValue
  }))
}
