export default {
  methods: {
    onChangeLocale (command) {
      this.$i18n.locale = command
      let message = `${this.$t('_name')} [ ${this.$i18n.locale} ]`
      if (['TRAVIS', 'NETLIFY'].includes(process.env.VUE_APP_BUILD_MODE)) {
        message = [
          `${this.$t('_name')} [ ${this.$i18n.locale} ]`,
          ` `,
          `<a class="el-link el-link--primary is-underline" target="_blank" href="https://fairyever.com/d2-admin/doc/zh/sys-locales/"> | D2Admin</a>`
        ].join('<br/>')
      }
      this.$notify({
        title: '',
        dangerouslyUseHTMLString: true,
        message
      })
    }
  }
}
