import layoutHeaderAside from '@/layout/header-aside'
import util from '@/utils'
/**
 * 
 */
const frameIn = [{
  path: '/',
  redirect: {
    name: 'index'
  },
  component: layoutHeaderAside,
  children: [
    // 
    {
      path: 'index',
      name: 'index',
      meta: {
        auth: true
      },
      component: util.import('system/index')
    },
    // 
    {
      path: 'page1',
      name: 'page1',
      meta: {
        title: ' 1',
        auth: true
      },
      component: util.import('demo/page1')
    },
    {
      path: 'page2',
      name: 'page2',
      meta: {
        title: ' 2',
        auth: true
      },
      component: util.import('demo/page2')
    },
    {
      path: 'page3',
      name: 'page3',
      meta: {
        title: ' 3',
        auth: true
      },
      component: util.import('demo/page3')
    },
    //  
    {
      path: 'log',
      name: 'log',
      meta: {
        title: '',
        auth: true
      },
      component: util.import('system/log')
    },
    //  
    {
      path: 'refresh',
      name: 'refresh',
      hidden: true,
      component: util.import('system/function/refresh')
    },
    //  
    {
      path: 'redirect/:route*',
      name: 'redirect',
      hidden: true,
      component: util.import('system/function/redirect')
    }
  ]
},
{
  path: '/home',
  name: 'home',
  component: util.import('system/home')
}
]

/**
 * 
 */
const frameOut = [
  // 
  {
    path: '/login',
    name: 'login',
    component: util.import('system/login')
  }
]

/**
 * 
 */
const errorPage = [{
  path: '*',
  name: '404',
  component: util.import('system/error/404')
}]

// 
export const frameInRoutes = frameIn

// 
export default [
  ...frameIn,
  ...frameOut,
  ...errorPage
]
