import {
  get
} from 'lodash'
import router from '@/router'
import setting from '@/setting.js'

// 
const isKeepAlive = data => get(data, 'meta.cache', false)

export default context => ({
  namespaced: true,
  state: {
    //  tab 
    pool: [],
    // 
    opened: setting.page.opened,
    //  https://github.com/d2-projects/d2-admin/issues/201
    openedLoaded: false,
    // 
    current: '',
    //  name
    keepAlive: []
  },
  actions: {
    /**
     * @description  https://github.com/d2-projects/d2-admin/issues/201
     * @param {Object} vuex context
     */
    isLoaded ({
      state
    }) {
      if (state.value) return Promise.resolve()
      return new Promise(resolve => {
        const timer = setInterval(() => {
          if (state.openedLoaded) {
            resolve(clearInterval(timer))
          }
        }, 10)
      })
    },
    /**
     * @class opened
     * @description 
     * @param {Object} vuex context
     */
    async openedLoad ({
      state,
      commit,
      dispatch
    }) {
      // store 
      const value = await dispatch('d2admin/db/get', {
        dbName: 'sys',
        path: 'page.opened',
        defaultValue: setting.page.opened,
        user: true
      }, {
        root: true
      })
      //  
      //  fullPath 
      // 
      // valid  1, 1, 0, 1 => , , , 
      const valid = []
      // 
      state.opened = value.map(opened => {
        // 
        if (opened.fullPath === '/index') {
          valid.push(1)
          return opened
        }
        //  name 
        const find = state.pool.find(item => item.name === opened.name)
        // 
        valid.push(find ? 1 : 0)
        //  
        //  params  query, 
        return Object.assign({}, opened, find)
      }).filter((opened, index) => valid[index] === 1)
      //  https://github.com/d2-projects/d2-admin/issues/201
      state.openedLoaded = true
      //  opened 
      commit('keepAliveRefresh')
    },
    /**
     *  opened   state.opened
     * @param {Object} vuex context
     */
    async opened2db ({
      state,
      dispatch
    }) {
      // 
      dispatch('d2admin/db/set', {
        dbName: 'sys',
        path: 'page.opened',
        value: state.opened,
        user: true
      }, {
        root: true
      })
    },
    /**
     * @class opened
     * @description 
     * @param {Object} vuex context
     * @param {Object} payload { index, params, query, fullPath } 
     */
    async openedUpdate ({
      state,
      commit,
      dispatch
    }, {
      index,
      params,
      query,
      fullPath
    }) {
      // 
      let page = state.opened[index]
      page.params = params || page.params
      page.query = query || page.query
      page.fullPath = fullPath || page.fullPath
      state.opened.splice(index, 1, page)
      // 
      await dispatch('opened2db')
    },
    /**
     * @class opened
     * @description  tag ()
     * @param {Object} vuex context
     * @param {Object} payload new tag info
     */
    async add ({
      state,
      commit,
      dispatch
    }, {
      tag,
      params,
      query,
      fullPath
    }) {
      //  tag 
      let newTag = tag
      newTag.params = params || newTag.params
      newTag.query = query || newTag.query
      newTag.fullPath = fullPath || newTag.fullPath
      // 
      state.opened.push(newTag)
      //  
      if (isKeepAlive(newTag)) {
        commit('keepAlivePush', tag.name)
      }
      // 
      await dispatch('opened2db')
    },
    /**
     * @class current
     * @description 
     * @param {Object} vuex context
     * @param {Object} payload  to  { name, params, query, fullPath } 
     */
    async open ({
      state,
      commit,
      dispatch
    }, {
      name,
      params,
      query,
      fullPath
    }) {
      // 
      let opened = state.opened
      //  
      let pageOpendIndex = 0
      const pageOpend = opened.find((page, index) => {
        const same = page.fullPath === fullPath
        pageOpendIndex = same ? index : pageOpendIndex
        return same
      })
      if (pageOpend) {
        // 
        await dispatch('openedUpdate', {
          index: pageOpendIndex,
          params,
          query,
          fullPath
        })
      } else {
        // 
        let page = state.pool.find(t => t.name === name)
        //  page  
        if (page) {
          await dispatch('add', {
            tag: Object.assign({}, page),
            params,
            query,
            fullPath
          })
        }
      }
      commit('currentSet', fullPath)
    },
    /**
     * @class opened
     * @description  tag ()
     * @param {Object} vuex context
     * @param {Object} payload { tagName:  }
     */
    async close ({
      state,
      commit,
      dispatch
    }, {
      tagName
    }) {
      // 
      let newPage = state.opened[0]
      const isCurrent = state.current === tagName
      // 
      if (isCurrent) {
        // 
        let len = state.opened.length
        for (let i = 1; i < len; i++) {
          if (state.opened[i].fullPath === tagName) {
            if (i < len - 1) {
              newPage = state.opened[i + 1]
            } else {
              newPage = state.opened[i - 1]
            }
            break
          }
        }
      }
      // 
      const index = state.opened.findIndex(page => page.fullPath === tagName)
      if (index >= 0) {
        //  
        commit('keepAliveRemove', state.opened[index].name)
        //  
        state.opened.splice(index, 1)
      }
      // 
      await dispatch('opened2db')
      // 
      if (isCurrent) {
        const {
          name = '', params = {}, query = {}
        } = newPage
        let routerObj = {
          name,
          params,
          query
        }
        router.push(routerObj)
      }
    },
    /**
     * @class opened
     * @description 
     * @param {Object} vuex context
     * @param {Object} payload { pageSelect: tagName }
     */
    async closeLeft ({
      state,
      commit,
      dispatch
    }, {
      pageSelect
    } = {}) {
      const pageAim = pageSelect || state.current
      let currentIndex = 0
      state.opened.forEach((page, index) => {
        if (page.fullPath === pageAim) {
          currentIndex = index
        }
      })
      if (currentIndex > 0) {
        //  
        state.opened.splice(1, currentIndex - 1).forEach(({
          name
        }) => commit('keepAliveRemove', name))
      }
      state.current = pageAim
      if (router.app.$route.fullPath !== pageAim) {
        router.push(pageAim)
      }
      // 
      await dispatch('opened2db')
    },
    /**
     * @class opened
     * @description 
     * @param {Object} vuex context
     * @param {Object} payload { pageSelect: tagName }
     */
    async closeRight ({
      state,
      commit,
      dispatch
    }, {
      pageSelect
    } = {}) {
      const pageAim = pageSelect || state.current
      let currentIndex = 0
      state.opened.forEach((page, index) => {
        if (page.fullPath === pageAim) {
          currentIndex = index
        }
      })
      //  
      state.opened.splice(currentIndex + 1).forEach(({
        name
      }) => commit('keepAliveRemove', name))
      // 
      state.current = pageAim
      if (router.app.$route.fullPath !== pageAim) {
        router.push(pageAim)
      }
      // 
      await dispatch('opened2db')
    },
    /**
     * @class opened
     * @description  tag
     * @param {Object} vuex context
     * @param {Object} payload { pageSelect: tagName }
     */
    async closeOther ({
      state,
      commit,
      dispatch
    }, {
      pageSelect
    } = {}) {
      const pageAim = pageSelect || state.current
      let currentIndex = 0
      state.opened.forEach((page, index) => {
        if (page.fullPath === pageAim) {
          currentIndex = index
        }
      })
      //  
      if (currentIndex === 0) {
        state.opened.splice(1).forEach(({
          name
        }) => commit('keepAliveRemove', name))
      } else {
        state.opened.splice(currentIndex + 1).forEach(({
          name
        }) => commit('keepAliveRemove', name))
        state.opened.splice(1, currentIndex - 1).forEach(({
          name
        }) => commit('keepAliveRemove', name))
      }
      // 
      state.current = pageAim
      if (router.app.$route.fullPath !== pageAim) {
        router.push(pageAim)
      }
      // 
      await dispatch('opened2db')
    },
    /**
     * @class opened
     * @description  tag
     * @param {Object} vuex context
     */
    async closeAll ({
      state,
      commit,
      dispatch
    }) {
      //  
      state.opened.splice(1).forEach(({
        name
      }) => commit('keepAliveRemove', name))
      // 
      await dispatch('opened2db')
      // 
      if (router.app.$route.name !== 'index') {
        router.push({
          name: 'index'
        })
      }
    }
  },
  mutations: {
    /**
     * @class keepAlive
     * @description 
     * @param {Object} state state
     */
    keepAliveRefresh (state) {
      state.keepAlive = state.opened.filter(item => isKeepAlive(item)).map(e => e.name)
    },
    /**
     * @description 
     * @param {Object} state state
     * @param {String} name name
     */
    keepAliveRemove (state, name) {
      const list = [...state.keepAlive]
      const index = list.findIndex(item => item === name)

      if (index !== -1) {
        list.splice(index, 1)
        state.keepAlive = list
      }
    },
    /**
     * @description 
     * @param {Object} state state
     * @param {String} name name
     */
    keepAlivePush (state, name) {
      const keep = [...state.keepAlive]
      keep.push(name)
      state.keepAlive = keep
    },
    /**
     * @description 
     * @param {Object} state state
     */
    keepAliveClean (state) {
      state.keepAlive = []
    },
    /**
     * @class current
     * @description  fullPath
     * @param {Object} state state
     * @param {String} fullPath new fullPath
     */
    currentSet (state, fullPath) {
      state.current = fullPath
    },
    /**
     * @class pool
     * @description  pool ()
     * @param {Object} state state
     * @param {Array} routes routes
     */
    init (state, routes) {
      const pool = []
      const push = function (routes) {
        routes.forEach(route => {
          if (route.children) {
            push(route.children)
          } else {
            if (!route.hidden) {
              const {
                meta,
                name,
                path
              } = route
              pool.push({
                meta,
                name,
                path
              })
            }
          }
        })
      }
      push(routes)
      state.pool = pool
    }
  }
})
