import {
  uniqueId
} from 'lodash'
// 
import setting from '@/setting.js'

/**
 *  path 
 * https://github.com/d2-projects/d2-admin/issues/209
 * @param {Array} menu 
 */
function supplementMenuPath (menu) {
  return menu.map(e => ({
    ...e,
    path: e.path || uniqueId('d2-menu-empty-'),
    ...e.children ? {
      children: supplementMenuPath(e.children)
    } : {}
  }))
}

export default context => ({
  namespaced: true,
  state: {
    // 
    header: [],
    // 
    aside: [],
    // 
    asideCollapse: setting.menu.asideCollapse
  },
  actions: {
    /**
     * 
     * @param {Object} vuex context
     * @param {Boolean} collapse is collapse
     */
    async asideCollapseSet ({
      state,
      dispatch
    }, collapse) {
      // store 
      state.asideCollapse = collapse
      // 
      await dispatch('d2admin/db/set', {
        dbName: 'sys',
        path: 'menu.asideCollapse',
        value: state.asideCollapse,
        user: true
      }, {
        root: true
      })
    },
    /**
     * 
     * @param {Object} vuex context
     */
    async asideCollapseToggle ({
      state,
      dispatch
    }) {
      // store 
      state.asideCollapse = !state.asideCollapse
      // 
      await dispatch('d2admin/db/set', {
        dbName: 'sys',
        path: 'menu.asideCollapse',
        value: state.asideCollapse,
        user: true
      }, {
        root: true
      })
    },
    /**
     * 
     * @param {Object} vuex context
     */
    async asideCollapseLoad ({
      state,
      dispatch
    }) {
      // store 
      state.asideCollapse = await dispatch('d2admin/db/get', {
        dbName: 'sys',
        path: 'menu.asideCollapse',
        defaultValue: setting.menu.asideCollapse,
        user: true
      }, {
        root: true
      })
    }
  },
  mutations: {
    /**
     * @description 
     * @param {Object} state state
     * @param {Array} menu menu setting
     */
    headerSet (state, menu) {
      // store 
      state.header = supplementMenuPath(menu)
    },
    /**
     * @description 
     * @param {Object} state state
     * @param {Array} menu menu setting
     */
    asideSet (state, menu) {
      // store 
      state.aside = supplementMenuPath(menu)
    }
  }
})
