import Vue from 'vue'
import router from '@/router'

export default context => ({
  namespaced: true,
  state: {
    // 
    value: '' // medium small mini
  },
  actions: {
    /**
     * @description  element
     * @param {Object} vuex context
     * @param {Boolean} refresh 
     */
    apply ({
      state,
      commit
    }, refresh) {
      Vue.prototype.$ELEMENT.size = state.value
      if (refresh) {
        commit('d2admin/page/keepAliveClean', null, {
          root: true
        })
        router.replace('/refresh')
      }
    },
    /**
     * @description  https://github.com/d2-projects/d2-admin/issues/198
     * @param {Object} vuex context
     */
    isLoaded ({
      state
    }) {
      if (state.value) return Promise.resolve()
      return new Promise(resolve => {
        const timer = setInterval(() => {
          if (state.value) {
            resolve(clearInterval(timer))
          }
        }, 10)
      })
    },
    /**
     * @description 
     * @param {Object} vuex context
     * @param {String} size 
     */
    async set ({
      state,
      dispatch
    }, size) {
      // store 
      state.value = size
      // 
      dispatch('apply', true)
      // 
      await dispatch('d2admin/db/set', {
        dbName: 'sys',
        path: 'size.value',
        value: state.value,
        user: true
      }, {
        root: true
      })
    },
    /**
     * @description 
     * @param {Object} vuex context
     */
    async load ({
      state,
      dispatch
    }) {
      // store 
      state.value = await dispatch('d2admin/db/get', {
        dbName: 'sys',
        path: 'size.value',
        defaultValue: 'mini',
        user: true
      }, {
        root: true
      })
      // 
      dispatch('apply')
    }
  }
})
