import {
  Message,
  MessageBox
} from 'element-ui'
import util from '@/utils'
import router from '@/router'
import api from '@/api'

export default context => ({
  namespaced: true,
  actions: {
    /**
     * @description 
     * @param {Object} vuex context
     * @param {Object} payload username {String} 
     * @param {Object} payload password {String} 
     * @param {Object} payload route {Object}   vue-router 
     */
    async login ({
      dispatch
    }, params) {
      try {
        const data = await api.USER_LOGIN(params)
        //  cookie  uuid  token  cookie
        // 
        // uuid    
        // token   token
        //  token 
        util.cookies.set('uuid', data.uuid)
        util.cookies.set('token', data.token)
        //  vuex 
        await dispatch('d2admin/user/set', data, {
          root: true
        })
        // 
        await dispatch('load')
        // 
        Message({
          message: '',
          type: 'success'
        })
        // 
        return Promise.resolve()
      } catch (error) {
        // 
        return Promise.reject(error)
      }
    },
    /**
     * @description 
     * @param {Object} vuex context
     * @param {Object} payload focus {Boolean}  
     * @param {Object} payload remote {Boolean} 
     * @param {Object} payload local {Boolean} 
     * @param {Object} payload back {Boolean} 
     */
    logout ({
      commit,
      dispatch
    }, {
      focus = false,
      remote = true,
      local = true,
      back = false
    } = {}) {
      /**
       * @description 
       */
      async function logout () {
        // 
        //  await 
        if (local) {
          //  cookie
          util.cookies.remove('token')
          util.cookies.remove('uuid')
          //  vuex 
          await dispatch('d2admin/user/set', {}, {
            root: true
          })
        }
        // 
        let redirect = ''
        if (back) {
          if (['login'].indexOf(router.app.$route.name) < 0) {
            redirect = router.app.$route.fullPath
          } else {
            redirect = router.app.$route.query.redirect
          }
        }
        router.push({
          name: 'login',
          query: {
            ...redirect ? {
              redirect
            } : {}
          }
        })
      }
      // 
      if (!focus) {
        commit('d2admin/gray/set', true, {
          root: true
        })
        MessageBox.confirm('', '', {
          type: 'warning'
        })
          .then(() => {
            commit('d2admin/gray/set', false, {
              root: true
            })
            logout()
          })
          .catch(() => {
            commit('d2admin/gray/set', false, {
              root: true
            })
            Message({
              message: ''
            })
          })
      } else {
        logout()
      }
    },
    /**
     * @description 
     * @param {Object} vuex context
     */
    async load ({
      dispatch
    }) {
      // DB -> store 
      await dispatch('d2admin/user/load', null, {
        root: true
      })
      // DB -> store 
      await dispatch('d2admin/theme/load', null, {
        root: true
      })
      // DB -> store 
      await dispatch('d2admin/transition/load', null, {
        root: true
      })
      // DB -> store 
      await dispatch('d2admin/page/openedLoad', null, {
        root: true
      })
      // DB -> store 
      await dispatch('d2admin/menu/asideCollapseLoad', null, {
        root: true
      })
      // DB -> store 
      await dispatch('d2admin/size/load', null, {
        root: true
      })
      // DB -> store 
      await dispatch('d2admin/color/load', null, {
        root: true
      })
    }
  }
})
